body {
    overflow: hidden;
}

.container {
    width: 95%;
    margin: 0 auto;
}

.login-form {
    padding-top: 180px;
    width: 450px;
    margin: 0 auto;
}

.ant-tabs-tab-active {
    font-weight: 400 !important;
}

.form .ant-form-item {
    margin-bottom: 15px !important;
}

.form .ant-form-item-label {
    line-height: 22px !important;
}

.form .ant-input-number-input,
.form .ant-input-number-input-wrap,
.form .ant-input-number {
    width: 100%;
}

.form .ant-calendar-picker {
    width: 100%;
}

.form label {
    display: block;
}