.nav-bar {
    margin-bottom: 30px;
    position: relative;
}

.nav-bar:before {
    content: '';
    position: absolute;
    background: #001529;
    top: 0;
    left: 0;
    right: -9999px;
    bottom: 0;
}

.nav-bar:after {
    content: '';
    position: absolute;
    background: #001529;
    top: 0;
    right: 100%;
    left: -9999px;
    bottom: 0;
}

.nav-logout {
    position: absolute;
    top: 11px;
    right: 13px;
    display: flex;
    align-items: center;
}

.nav-user-name {
    color: #fff;
    margin-right: 15px;
}